/*
 * # Stylesheets
 * Each block lives in its own stylesheet to keep things separate and easy to
 * manage. This site uses BEM-style class names.
 */

/*
 * ## Site-Wide and Config Styles
 */

/*
 * ## Media Queries
 */

* {
  margin: 0;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*+* {
  margin-top: 15px;
}

body {
  margin-top: 0;
  background-color: #fff;
}

/* iOS adds weird form styles that we don't want here. */

input:not([type=checkbox]),input:not([type=radio]),textarea,select {
  -webkit-appearance: none;
  border-radius: 3px;
}

html,
body {
  font-family: brandon-grotesque,sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
}

@media (min-width: 768px) {

  html,
body {
    font-size: 20px;
  }
}

a {
  color: #25e493;
  font-weight: 700;
  text-decoration: none;
}

a:active,
  a:hover,
  a:focus {
  outline: 0;
  text-decoration: underline;
}

.wp-caption {
  max-width: 100% !important;
}

.wp-caption img {
  max-width: 100%;
  height: auto;
}

.wp-caption-text,
.wp-caption-dd {
  display: block;
  margin-top: 0;
  padding: 0 5px;
  color: rgb(135, 137, 138);
  font-size: 80%;
  font-style: italic;
  line-height: 1.25;
}

.aligncenter,
.alignleft,
.alignright {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  border: 1px solid rgba(37, 228, 147, 0.15);
}

figure.aligncenter, figure.alignleft, figure.alignright {
  margin-top: 32px;
  margin-top: 2rem;
  border: 0;
}

figure.aligncenter img, figure.alignleft img, figure.alignright img {
  border: 1px solid rgba(37, 228, 147, 0.15);
}

figure.aligncenter,
dl.aligncenter {
  margin-top: 32px;
  margin-top: 2rem;
}

@media (min-width: 800px) {

  figure.aligncenter,
dl.aligncenter {
    text-align: center;
  }
}

img.alignleft,
img.alignright,
img.aligncenter {
  max-width: 100%;
  height: auto;
}

img.aligncenter {
  margin-top: 30px;
}

.alignleft,
  figure.alignleft,
  img.alignleft,
  dl.alignleft,
  .alignright,
  figure.alignright,
  img.alignright,
  dl.alignright {
  margin-top: 30px;
}

@media (min-width: 768px) {

  .alignleft,
  figure.alignleft,
  img.alignleft,
  dl.alignleft,
  .alignright,
  figure.alignright,
  img.alignright,
  dl.alignright {
    max-width: 45% !important;
  }
}

@media (min-width: 768px) {

  .alignleft,
  figure.alignleft,
  img.alignleft,
  dl.alignleft {
    float: left;
    margin-right: 30px;
  }
}

@media (min-width: 768px) {

  .alignright,
  figure.alignright,
  img.alignright,
  dl.alignright {
    float: right;
    margin-left: 30px;
    text-align: right;
  }
}

.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

