}

.search-field:focus,
  .search-field:hover {
  outline: none;
  background-color: rgba(37, 228, 147, 0.05);
  border-color: rgb(135, 137, 138);
}

.search-submit {
  display: inline-block;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 8px;
  margin-top: 0.5rem;
  padding: 0 16px;
  padding: 0 1rem;
  background-color: #fff;
  border: 3px solid #25e493;
  border-radius: 3px;
  color: #25e493;
  cursor: pointer;
  font-family: brandon-grotesque,sans-serif;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: 700;
  letter-spacing: 1px;
  letter-spacing: 0.1rem;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transition: 150ms all linear;
          transition: 150ms all linear;
}

.search-submit:hover,
  .search-submit:active,
  .search-submit:focus {
