      .gform_wrapper .ginput_container select:focus,
      .gform_wrapper .ginput_container select:hover,
      .gform_wrapper .ginput_container textarea:focus,
      .gform_wrapper .ginput_container textarea:hover {
  outline: none;
  background-color: rgba(37, 228, 147, 0.05);
  border-color: rgb(135, 137, 138);
}

.gform_wrapper .ginput_container textarea {
  height: 112px;
  height: 7rem;/*
      &.small {

      }
      */
}

.gform_wrapper .ginput_container.ginput_complex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.gform_wrapper .ginput_container.ginput_complex::before,
      .gform_wrapper .ginput_container.ginput_complex::after {
  content: " ";
