  content: '';
  display: block;
  position: absolute;
  background-color: #25e493;
}

.testimonial__image-wrap::before {
  top: calc(50% - 4px);
  width: 8px;
  height: 8px;
  border-radius: 50%;
}

.testimonial__image-wrap::after {
  top: calc(50% - 1px);
  width: 32px;
